import React, { useState, useEffect, useRef } from "react";
import { Link, useLocation, useNavigate } from "react-router-dom";
import { supabase } from "../lib/supabaseClient";
import "./SiteHeader.css";

export default function SiteHeader() {
  const [menuOpen, setMenuOpen] = useState(false);
  const [user, setUser] = useState(null);
  const [showAuthModal, setShowAuthModal] = useState(false);
  const [isLoginMode, setIsLoginMode] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");
  const [suggestions, setSuggestions] = useState([]);
  const [history, setHistory] = useState([]);
  const [showDropdown, setShowDropdown] = useState(false);

  const location = useLocation();
  const navigate = useNavigate();
  const dropdownRef = useRef(null);

  const PRESET_SUGGESTIONS = ["", "", "", "", "", ""];

  //  
  useEffect(() => {
    const savedUser = JSON.parse(localStorage.getItem("user") || "null");
    if (savedUser) setUser(savedUser);
  }, []);

  //  
  useEffect(() => {
    const saved = JSON.parse(localStorage.getItem("searchHistory") || "[]");
    setHistory(saved);
  }, []);

  //  
  useEffect(() => {
    const handleClickOutside = (e) => {
      if (dropdownRef.current && !dropdownRef.current.contains(e.target)) {
        setShowDropdown(false);
      }
    };
    document.addEventListener("mousedown", handleClickOutside);
    return () => document.removeEventListener("mousedown", handleClickOutside);
  }, []);

  //  
  const handleLogin = (e) => {
    e.preventDefault();
    const email = e.target.elements.email.value.trim();
    const name = email.split("@")[0];
    const isAdmin = email === "admin@love.com";

    const loggedInUser = { name, email, isAdmin };
    setUser(loggedInUser);
    localStorage.setItem("user", JSON.stringify(loggedInUser));
    setShowAuthModal(false);
  };

  //  
  const handleLogout = () => {
    setUser(null);
    localStorage.removeItem("user");
    navigate("/");
  };

  //  
  const handleSearch = (term) => {
    if (!term.trim()) return;
    //  5 
    const newHistory = [term, ...history.filter((h) => h !== term)].slice(0, 5);
    setHistory(newHistory);
    localStorage.setItem("searchHistory", JSON.stringify(newHistory));
    navigate(`/tests?q=${encodeURIComponent(term.trim())}`);
    setShowDropdown(false);
    setMenuOpen(false);
  };

  //  
  useEffect(() => {
    const kw = searchTerm.trim().toLowerCase();
    if (!kw) {
      setSuggestions([]);
      return;
    }
    const match = PRESET_SUGGESTIONS.filter((s) =>
      s.toLowerCase().includes(kw)
    );
    setSuggestions(match);
  }, [searchTerm]);

  //  
  const isActive = (path) => location.pathname === path;

  return (
    <header className="site-header">
      <div className="header-container">
        {/*  Logo */}
        <div className="logo" onClick={() => navigate("/")}>
           
        </div>

        {/*   */}
        <nav className={`nav-links ${menuOpen ? "open" : ""}`}>
          <Link className={isActive("/") ? "active" : ""} to="/">
            
          </Link>
          <Link className={isActive("/tests") ? "active" : ""} to="/tests">
            
          </Link>
          {user?.isAdmin && (
            <Link className={isActive("/admin") ? "active" : ""} to="/admin">
              
            </Link>
          )}
        </nav>

        {/*   */}
        <div className="search-wrapper" ref={dropdownRef}>
          <form
            className="search-bar"
            onSubmit={(e) => {
              e.preventDefault();
              handleSearch(searchTerm);
            }}
          >
            <input
              type="text"
              placeholder="..."
              value={searchTerm}
              onChange={(e) => {
                setSearchTerm(e.target.value);
                setShowDropdown(true);
              }}
              onFocus={() => setShowDropdown(true)}
            />
            <button type="submit"></button>
          </form>

          {/*   &  */}
          {showDropdown && (
            <div className="search-dropdown">
              {searchTerm ? (
                suggestions.length > 0 ? (
                  <ul>
                    {suggestions.map((s, i) => (
                      <li key={i} onClick={() => handleSearch(s)}>
                         {s}
                      </li>
                    ))}
                  </ul>
                ) : (
                  <p className="no-suggestion"></p>
                )
              ) : history.length > 0 ? (
                <>
                  <div className="history-header">
                    <span> </span>
                    <button
                      onClick={() => {
                        localStorage.removeItem("searchHistory");
                        setHistory([]);
                      }}
                    >
                      
                    </button>
                  </div>
                  <ul>
                    {history.map((h, i) => (
                      <li key={i} onClick={() => handleSearch(h)}>
                        {h}
                      </li>
                    ))}
                  </ul>
                </>
              ) : (
                <p className="no-suggestion"></p>
              )}
            </div>
          )}
        </div>

        {/*   */}
        <div className="auth-section">
          {user ? (
            <>
              <span className="welcome">
                {user.name}
                {user.isAdmin && <span className="admin-tag"></span>}
              </span>
              <button className="logout-btn" onClick={handleLogout}>
                
              </button>
            </>
          ) : (
            <button
              className="login-btn"
              onClick={() => {
                setIsLoginMode(true);
                setShowAuthModal(true);
              }}
            >
               / 
            </button>
          )}
        </div>

        {/*   */}
        <button
          className={`menu-toggle ${menuOpen ? "active" : ""}`}
          onClick={() => setMenuOpen(!menuOpen)}
        >
          
        </button>
      </div>

      {/*   */}
      {showAuthModal && (
        <div className="auth-modal">
          <div className="auth-box">
            <h3>{isLoginMode ? "" : ""}</h3>
            <form onSubmit={handleLogin}>
              <input name="email" type="email" placeholder="" required />
              <input name="password" type="password" placeholder="" required />
              <button type="submit" className="btn-primary">
                {isLoginMode ? "" : ""}
              </button>
            </form>
            <p>
              {isLoginMode ? "" : ""}{" "}
              <span
                className="switch-mode"
                onClick={() => setIsLoginMode(!isLoginMode)}
              >
                {isLoginMode ? "" : ""}
              </span>
            </p>
            <button className="close-modal" onClick={() => setShowAuthModal(false)}>
              
            </button>
          </div>
        </div>
      )}
    </header>
  );
}
